<template lang="">
<div class="_full_router financial-content">
<!-- <mt-header :title="title"><mt-button icon="back" @click="goBack" slot="left">返回</mt-button></mt-header> -->
<custHeader :title="title"></custHeader>
<div class="jqqd" v-if="list.length==0">
                {{tipMessage}}
            </div>
 <mt-loadmore :top-method="refresh" :bottom-method="loadMore" :bottom-all-loaded="loading" ref="loadmore"  >
  <ul class="list-infinite cancel-link"  >
    <li v-for="(item,index) in list">
      <router-link :to="{path:'/financial/productDetail',query:{issNo:item.issNo,stkBiz:stkBiz}}">
          <div class="financial-box">
            <div>{{item.stkName}}</div>
            <div class="circle-progress-contain">
              <circleProgress :num="jindu(item)" ></circleProgress>
            </div>

          </div>
          <div class="financial-box little-tile">
            <div>
              价格:<span clsss="big-info" style="font-size: 18px;color: #f00;">{{item.issPrice | toMoney}}</span>元/<span v-remoteoptionsspan="dicts.unit">{{item.unit}}</span>
            </div>
            <div style="height: 20px;line-height: 22px;">
              梯度:{{item.baseQty}}

            </div>
            <div style="height: 20px;line-height: 22px;">
              可购:{{kegou(item)}}

            </div>
          </div>
    </router-link>
    </li>
  </ul>
</mt-loadmore>
  <transition name="slide-right">
    <router-view class="cover-transition"></router-view>
  </transition>
</div>
</template>
<script>
import cookieUtil from "js-cookie";
import custMixin from  'util/custMixin'
import custHeader from  'components/custHeader'
import {
  Navbar,
  TabItem,
  Loadmore,
  Header
} from "mint-ui";
import {
  Toast
} from "mint-ui";
Vue.use(Loadmore.name,Loadmore);
Vue.component(Header.name,Header);

import circleProgress from 'components/circleProgress.vue'
export default {
  data() {
    return {
        dicts: {
          unit: {
            name: "UNIT"
          }
        },
    title:"",
      tipMessage:"加载中...",
      cacheList: {},
      loading:true,
      list: [
    //   {
    //     stkName: "",
    //     code: "",
    //     expectRate: "",
    //     issPrice: "",
    //     subscribedLowAmt: ""
    // }
    ],
    allList:[],
    serverConfig:{order:'',//排序 不填 默认字段
              stkex:global.scdm,//
              expectRateFlag:0,// 预期年绿化率" 0 全部
              minBuyAmt:0,//起购金额 0不限
              periodFlag:0,//剩余期限//0不限
              offset:0,
              limit:global.pageSize
          },
    stkBiz:global.STKBIZ.SUBSCRIBE      //资金申购  默认这个  不做认购申购
    };
  },
  mixins:[custMixin],
  created: function() {
     if(this.$route.query.stkBiz){
      this.stkBiz=this.$route.query.stkBiz;
      switch(this.stkBiz){
        case global.STKBIZ.SUBSCRIBE:
           this.title="资金申购列表";
           break;
        case global.STKBIZ.SUBSCRIBE_PURCHASE:
           this.title="认购列表";
           break;
           case global.STKBIZ.DECLARE_ISSUE:
           this.title="配股申报";
           break;
           case global.STKBIZ.MARKET_SELL:
           this.title="市值配售";
           break;
           case global.STKBIZ.PRIVATE_SELL:
           this.title="定向配售";
           break;
        default :
            this.title="资金申购列表";

      }

      this.serverConfig.stkBiz=this.stkBiz;
     }
    this.getDataList().then(data=>{
       this.allList=data.rows;
       
    });

  },
  components:{
    custHeader,
    circleProgress
  },
  methods: {
      kegou: function(item) {
        if (!item.applyAmt)
          item.applyAmt = 0;
        return item.issSize.subtr(item.applyAmt)
      },
      jindu: function(item) {
        // return item.applyAmt.div(item.issSize)
        if (!item.applyAmt)
          item.applyAmt = 0;
        return item.applyAmt.div(item.issSize)
      },
    refresh:function(){
      this.serverConfig.offset=0;
      this.getDataList().then(data=>{
       this.allList=data.rows;
       this.$refs.loadmore.onTopLoaded();
       if(data.total==this.allList.length){
        this.loading=true;
       }else{
        this.loading=false;
       }
    });

    },
    financialDetail: function(index) {
      //this.$router.push("/financial/detail?issNo=" + this.list[index].issNo);
    },
    getDataList:function(){

      var self = this;
      console.log(this.serverConfig.offset,"offfffffffff")
   return  axiosUtil.upCltIssStkQry(this.serverConfig).then(function(data) {
      var data=data.data;
      //if(data.errorcode==0){

          // if(self.$route.query.stkBiz){
          //     var items=[]
          //         data.rows.forEach(function(item){
          //             var issAmt = item.issAmt?item.issAmt:'0';//发行额度
          //             var applyAmt = item.applyAmt?item.applyAmt:'0';//已申购金额
          //             var fundAppQty=parseFloat(item.fundAppQty?item.fundAppQty:0);//资金申购数
          //             var subsribedAllotmentQty=parseFloat(item.subsribedAllotmentQty?item.subsribedAllotmentQty:0);//认购交款数
          //              if(self.$route.query.stkBiz==global.STKBIZ.SUBSCRIBE){//资金申购
          //                   if(fundAppQty>0){
          //                         items.push(item);
          //                       }
          //                }else if(self.$route.query.stkBiz==global.STKBIZ.SUBSCRIBE){//认购
          //                   if(subsribedAllotmentQty>0){
          //                       items.push(item);
          //                     }
          //                }

          //         })
          //         self.list= items;

          // }else{
             self.list = data.rows//默认所有
        //  }
          if( self.list.length==0){
           self.tipMessage="暂无数据"
          }
          return data;
    })

    },
    loadMore: function() {
        let self = this;
        if(!this.loading){
          this.loading = true;
        this.serverConfig.offset += global.pageSize;
        this.getDataList(data=>{
          self.allList = self.allList.concat(data.rows);
           self.loading = false;
           if (data.total == self.allList.length) {
             self.loading = true;
             Toast({
               message: "已加载最后一条",
               position: "bottom",
               duration: 1000
             });
           }
        })
        }
        
        // axiosUtil.upCltIssStkQry(this.serverConfig).then(function(data) {
        //     var data = data.data;
        //     self.allList = self.allList.concat(data.rows);
        //    self.loading = false;
        //    if (data.total == self.list.length) {
        //      self.loading = true;
        //      Toast({
        //        message: "已加载最后一条",
        //        position: "bottom",
        //        duration: 1000
        //      });
        //    }
        // })
    }
  }
};
</script>
<style scoped>
.financial-box {
  display: -webkit-flex;
  /* Safari */
  display: flex;
}

.financial-box>div {
  flex-grow: 1;
}

.nhsyl {
  font-size: 26px;
  color: #ef4f4f;
}

.nhsyl-text {
  font-size: 14px;
  color: #bbb;
}

.qx {
  margin-top: 12px;
}

.qx-text {
  font-size: 14px;
  color: #7089ff;
  border-right: 1px solid #ddd;
  padding: 0 4px;
  margin-right: 4px;
}

.app-content {
  height: 100%;
  background: #111;
}

.financial-content {
  height: 100%;
  background: #efeee9;
}

::-webkit-scrollbar {
  width: 0px;
  height: 1px;
}

::-webkit-scrollbar-thumb {
  border-radius: 5px;
  -webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
  background: rgba(0, 0, 0, 0.2);
}

.cancel-link  a{
  text-decoration: none;
  color: #111;
}
.circle-progress-contain>div {
  float: right;
}
.list-infinite{
    width: 100%;
}
</style>



