<template>

	  <div class="circle-progress">
                <div class="circle"  :class="{clipAuto:showAll(num)}">
                  <div class="circle-percent circle-percent-left" :style="{transform:progressDu(num)}"></div>
                  <div class="circle-percent circle-percent-right " :class="{wth0:hideRigth(num)}"></div>
                </div>
                <div class="progress-num"><span>{{jd}}</span>%</div>
            </div>
</template>
<script>
	export default{
		data(){
			return {

			}
		},
		props:{
				num:{
					type:Number,
					default:0,
				}
		},
    computed:{
      jd:function(){
        return (this.num*100).toFixed(0);
      }
    },
		methods:{
			hideRigth:function(num){
      return num<0.5
    },
    showAll:function(num){
      return num>0.5
    },
    progressDu:function(num){
       let deg=num*360
      return "rotate("+deg+"deg)"
    },

		}
	}

</script>
<style type="text/css" scoped> 
	.circle-progress{
  width: 60px;
  height: 60px;
  position: relative;
  background: #ccc;
  border-radius: 50%;
}
.circle,.circle-percent{
  position: absolute;
  width: 60px;
  height: 60px;
  border-radius:50%;
}
.circle{
  box-sizing:border-box;
  border: 4px solid #ccc;
  clip:rect(0,60px,60px,30px);
}
.clipAuto{
  clip: rect(auto,auto,auto,auto);
}

.circle-percent{
  box-sizing:border-box;
  top: -4px;
  left: -4px;

}
.circle-percent-left{
  transition:transform ease;
  border: 4px solid #EF4F4F;
  clip:rect(0,30px ,60px, 0);
}

.circle-percent-right{
   border: 4px solid #EF4F4F;
  clip:rect(0,60px ,60px, 30px);
}

.wth0{
  width: 0;
}

.progress-num{
position: absolute;
box-sizing:border-box;
width: 52px;
height: 52px;
line-height: 52px;
text-align: center;
font-size: 14px;
left: 4px;
top: 4px;
border-radius: 50%;
background: #fff;
}
</style>


